<template>
  <div id="scanQRCode">
    <b-container fluid>
      <div id="qr-reader" style="width: 400px;height: 400px;">
      <StreamBarcodeReader @decode="onDecode" @loaded="onLoaded"></StreamBarcodeReader>
      </div>
    </b-container>
    Scan Result: {{scanResult}}
  </div>
</template>

<script>

import { StreamBarcodeReader } from "vue-barcode-reader";
import BarCodeService from '../services/BarCodeService';

export default {
  name: "scanBarCode",
  components: {
    StreamBarcodeReader,
  },
  data() {
    return {
      scanResult: "",
      beepSound: null
    };
  },

  mounted() {
    this.beepSound = new Audio("Barcode-scanner-beep-sound.mp3");
    //this.beepSound.play();
  },

  methods: {
    onLoaded () {
      console.log("StreamBarcodeReader loaded");
    },

    onDecode (decodedString) {
      this.scanResult = decodedString;
      console.log(decodedString);
      //alert(this.scanResult)

      let barcode = this.scanResult;
      BarCodeService.scan(barcode).then(resp => {
        let result = resp.data;
        console.log(result);

        this.beepSound.play();
        alert(result)
      });
    }
  },
};
</script>

<style>
body {
  font-family: Arial, Helvetica, sans-serif;
}
</style>
